import './RulesListCompany.styl';
import PropTypes from 'prop-types';

import React, {
  Component,
} from 'react';

import Company from './Company/Company';

export default class RulesListCompany extends Component {
  static propTypes = {
    checkShow: PropTypes.bool,
    status: PropTypes.bool,
    data: PropTypes.array,
    actions: PropTypes.object,
    company: PropTypes.object
  };

  render() {
    const {
      checkShow,
      status,
      data,
      actions,
      company
    } = this.props;

    return (
      <div className="c-rule-list-company">
        {
          data.map((item) => (
            <Company
              company={item}
              key={item.id}
              checkShow={checkShow}
              statusCompany={status}
              actions={actions}
              active={company && company.id === item.id}
              companyObject={company && company}
            />
          ))
        }
      </div>
    );
  }
}



