import './ReportsList.styl';

import React, {
  Component,
} from 'react';
import Toggle from 'material-ui/Toggle';
import facebook from '../../../images/icons/big/facebook-2.svg';
import FloatingActionButton from 'material-ui/FloatingActionButton';
import ContentAdd from 'material-ui/svg-icons/content/add';
import { Link } from 'react-router-dom';
import PropType from 'prop-types';

const stylesToggle = {
  block: {
    maxWidth: 250,
  },
  toggle: {
    marginBottom: 16,
  },
  thumbOff: {
    backgroundColor: '#fff',
  },
  trackOff: {
    backgroundColor: '#e0e8ee',
  },
  thumbSwitched: {
    backgroundColor: '#fff',
  },
  trackSwitched: {
    backgroundColor: '#5bb995',
  },
  labelStyle: {
    color: '#678198',
    fontSize: '12px',
    textAlign: 'right'
  },
};

const styleButton = {
  marginLeft: '40px',
  marginTop: '13px'
};

export default class ReportsList extends Component {
  static propTypes = {
    data: PropType.array,
    reportActions: PropType.object
  };

  constructor(props) {
    super(props);

    this.state = {
    };
  }

  updateCheck(index) {
    const reports = this.state.reports;
    reports[index].status = !reports[index].status;
    this.setState({ reports });
  }

  toggleText(index) {
    const {
      data
    } = this.props;

    return (
      <span className="status-toggle">
        Report status :
        <span className="controller-toggle">{data[index].status ? 'ON' : 'OFF'}</span>
      </span>
    );
  }

  render() {
    const {
      data,
    } = this.props;

    const {
      setStatus
    } = this.props.reportActions;

    return (
      <div className="c-reports-list">
        {
          data.map((item, index) => (
            <div className="report" key={index}>
              <div className="title-report">
                <span className="name">{item.name}</span>
                <div className="report-toggle-container">
                  <Toggle
                    label={this.toggleText(index)}
                    thumbStyle={stylesToggle.thumbOff}
                    trackStyle={stylesToggle.trackOff}
                    thumbSwitchedStyle={stylesToggle.thumbSwitched}
                    trackSwitchedStyle={stylesToggle.trackSwitched}
                    labelStyle={stylesToggle.labelStyle}
                    toggled={item.status}
                    onToggle={() => setStatus(data, index)}
                  />
                </div>
              </div>
              <span className="report-text">Chanels</span>
              <div className="chanels">
                <div className="chanel">
                  <div className="chanel-img">
                    <img alt="/" src={facebook} />
                  </div>
                  <span className="chanel-name">Facebook</span>
                </div>
              </div>
              <span className="report-text">Collect period</span>
              <span className="period">{item.collection}</span>
            </div>
          ))
        }
        <Link to="/new-report">
          <FloatingActionButton
            style={styleButton}
            backgroundColor="#f07171"
          >
            <ContentAdd />
          </FloatingActionButton>
        </Link>
      </div>
    );
  }
}



