import './HelpContent.styl';
import _find from 'lodash/find';

import React, {
  Component,
} from 'react';

import PropTypes from 'prop-types';

const content = [
  {
    title: 'How to create a Rule?',
    link: '/help',
    content: [
      {
        text: `To create a rule go to Rules section and click plus button.
        After this create your rule either via advanced rules visual constructor or just
        type your rule into rule code section.`
      }
    ]
  },
  {
    title: 'How to create a Report',
    link: '/help/two',
    content: [
      {
        text: `To create a report go to Reports section and click plus button.
        Choose appropriate Report template, reports periods and notification sources.`
      }
    ]
  },
  {
    title: 'CTR Threshold3',
    link: '/help/three',
    content: [
      {
        img: '',
        text: `33You can download Photoshop from the Creative Cloud apps catalog. 
        You’ll need to sign in with your Adobe ID and password to complete the download. 
        For more information, including how to install older versions or check for 
        updates, see Download and install Creative Cloud apps.`
      },
      {
        img: '',
        text: 'Creative Cloud apps catalog. You’ll need to' +
        ' sign in with your Adobe ID and password to complete the download.' +
        ' For more information, including how to install older versions or' +
        ' check for updates, see Download and install Creative Cloud apps.'
      }
    ]
  },
  {
    title: 'CTR Threshold4',
    link: '/help/four',
    content: [
      {
        img: '',
        text: `44You can download Photoshop from the Creative Cloud apps catalog. 
        You’ll need to sign in with your Adobe ID and password to complete the download. 
        For more information, including how to install older versions or check for 
        updates, see Download and install Creative Cloud apps.`
      },
      {
        img: '',
        text: 'Creative Cloud apps catalog. You’ll need to' +
        ' sign in with your Adobe ID and password to complete the download.' +
        ' For more information, including how to install older versions or' +
        ' check for updates, see Download and install Creative Cloud apps.'
      }
    ]
  }
];

export default class HelpContent extends Component {
  static propTypes = {
    path: PropTypes.string
  };

  render() {
    const {
      path
    } = this.props;
    const show = _find(content, { link: path });

    return (
      <div className="c-help-content">
        <span className="title">{show.title}</span>
        {
          show.content.map((item, index) => (
            <div className="content-container" key={index}>
              <div className="numb">{index + 1}.</div>
              <div className="content">
                <p className="text">{item.text}</p>
                {
                  item.img &&
                    <div className="image-container">
                      <img alt="/" src={item.img} />
                    </div>
                }
              </div>
            </div>
          ))
        }
      </div>
    );
  }
}



