import './ContactContent.styl';
import logo from '../../../images/logo-black.png';

import React, {
  Component,
} from 'react';

import TextField from 'material-ui/TextField';
import FloatingActionButton from 'material-ui/FloatingActionButton';
import ContentSend from 'material-ui/svg-icons/content/send';
import { Link } from 'react-router-dom';

const content = {
  text: `You can download Photoshop from the 
  Creative Cloud apps catalog. You’ll need to sign 
  in with your Adobe ID and password to complete the download. 
  For more information, including how to install older versions or 
  check for updates, see Download and install Creative Cloud apps.`,
  address: 'hi@adapty.com'
};

const stylesField = {
  style: {
    color: '#000000',
    width: '100%',
    fontSize: '16px'
  },
  floatingLabelStyle: {
    color: '#000000',
  },
  underlineStyle: {
    borderColor: '#d1dfea'
  },
  underlineFocusStyle: {
    borderColor: '#678198'
  },
  floatingLabelFocusStyle: {
    color: '#678198',
  },
};

const style = {
  position: 'absolute',
  bottom: '-28px',
  right: '68px'
};

export default class ContactContent extends Component {
  static propTypes = {
  };

  render() {
    return (
      <div className="c-contact-content">
        <div className="logo-content">
          <img alt="/" src={logo} />
        </div>
        <div className="container-info">
          <div className="left">
            <p className="text">{content.text}</p>
            <div className="social-info">
              <span className="address">{content.address}</span>
            </div>
          </div>
          <div className="right">
            <span className="title">Feel free to text us</span>
            <div className="text-field-container">
              <TextField
                hintText="How can we help?"
                floatingLabelText="How can we help?"
                style={stylesField.style}
                floatingLabelStyle={stylesField.floatingLabelStyle}
                underlineStyle={stylesField.underlineStyle}
                floatingLabelFocusStyle={stylesField.floatingLabelFocusStyle}
                underlineFocusStyle={stylesField.underlineFocusStyle}
                multiLine
                rows={1}
              />
            </div>
          </div>
        </div>
        <Link to="/dashboard">
          <FloatingActionButton
            style={style}
            backgroundColor="#5bb995"
          >
            <ContentSend />
          </FloatingActionButton>
        </Link>
      </div>
    );
  }
}



