import * as actionTypes from '../constants/ReportConstants';
import _cloneDeep from 'lodash/cloneDeep';

export function setStatus(array, index) {
  const reportData = _cloneDeep(array);

  reportData[index].status = !reportData[index].status;
  return (dispatch) => {
    dispatch({
      type: actionTypes.SET_REPORT_STATUS,
      reportData
    });
  };
}



