import './LoginForm.styl';
import React, {
  Component,
} from 'react';
import TextField from 'material-ui/TextField';
import logo from '../../../images/logo-black.png';
import facebook from '../../../images/facebook.png';
import { Link } from 'react-router-dom';
import Checkbox from 'material-ui/Checkbox';
import PropTypes from 'prop-types';

const stylesField = {
  style: {
    color: '#000000',
    width: '100%',
    fontSize: '16px'
  },
  floatingLabelStyle: {
    color: '#000000',
  },
  underlineStyle: {
    borderColor: '#d1dfea'
  },
  underlineFocusStyle: {
    borderColor: '#678198'
  },
  floatingLabelFocusStyle: {
    color: '#678198',
  },
};

const styleCheckBox = {
  style: {
    width: '180px',
    marginLeft: '-5px'
  },
  labelStyle: {
    color: '#000',
    fontSize: '16px',
    marginLeft: '-8px'
  },
  iconStyle: {
    fill: '#678198',
    width: '16px',
    height: '16px'
  }
};

export default class LoginForm extends Component {
  static propTypes = {
    path: PropTypes.string,
    email: PropTypes.string
  };

  constructor(props) {
    super(props);

    this.state = {
      checked: false,
    };
  }

  updateCheck() {
    this.setState({ checked: !this.state.checked });
  }

  render() {
    const {
      checked
    } = this.state;

    const {
      path,
      email
    } = this.props;

    const colorCheckBox = checked ? '#5bb995' : '#678198';

    return (
      <div className="c-login-form">
        <div className="top-login-form">
          <div className="logo-login-form">
            <img alt="/" src={logo} />
          </div>
          <TextField
            hintText="Email"
            floatingLabelText="Email"
            style={stylesField.style}
            floatingLabelStyle={stylesField.floatingLabelStyle}
            underlineStyle={stylesField.underlineStyle}
            floatingLabelFocusStyle={stylesField.floatingLabelFocusStyle}
            underlineFocusStyle={stylesField.underlineFocusStyle}
            value={email}
          />
          <TextField
            hintText="Password"
            floatingLabelText="Password"
            style={stylesField.style}
            floatingLabelStyle={stylesField.floatingLabelStyle}
            underlineStyle={stylesField.underlineStyle}
            floatingLabelFocusStyle={stylesField.floatingLabelFocusStyle}
            underlineFocusStyle={stylesField.underlineFocusStyle}
            value="123456"
            type="password"
          />
          {
            path === '/' &&
              <Link to="/" className="forgot-password">Forgot password</Link>
          }
          <div className={`login-submit-container ${ path !== '/' && 'registration' }`}>
            {
              path === '/' &&
                <Checkbox
                  label="Remember me"
                  style={styleCheckBox.style}
                  labelStyle={styleCheckBox.labelStyle}
                  iconStyle={{ fill: colorCheckBox }}
                  onCheck={this.updateCheck.bind(this)}
                  checked={checked}
                />
            }
            <Link to="/dashboard" className="login-button">
              {
                path === '/' ? 'LOG IN' : 'SIGN UP'
              }
            </Link>
          </div>
        </div>
        <div className="bottom-login-form">
          <Link
            className="login-this-facebook"
            to="/dashboard"
          >
            <img alt="/" className="fb-icon" src={facebook} />
            {
              path === '/' ? 'Log In with facebook' : 'Sign Up with facebook'
            }
          </Link>
        </div>
      </div>
    );
  }
}



