import './EventsDashboard.styl';

import React, {
  Component,
} from 'react';

import facebook from '../../../images/icons/facebook/facebook-grey.svg';

const events = [
  {
    day: 'today',
    eventsList: [
      {
        name: 'Return of Xander',
        time: '20 : 38',
        rule: 'Minimum value of transitions'
      },
      {
        name: 'Return of Xander',
        time: '20 : 38',
        rule: 'Minimum value of transitions'
      },
      {
        name: 'Return of Xander',
        time: '20 : 38',
        rule: 'Minimum value of transitions'
      }
    ]
  },
  {
    day: 'Yesterday',
    eventsList: [
      {
        name: 'Return of Xander',
        time: '20 : 38',
        rule: 'Minimum value of transitions'
      },
      {
        name: 'Return of Xander',
        time: '20 : 38',
        rule: 'Minimum value of transitions'
      },
      {
        name: 'Return of Xander',
        time: '20 : 38',
        rule: 'Minimum value of transitions'
      }
    ]
  }
];

export default class EventsDashboard extends Component {
  static propTypes = {
  };

  render() {
    return (
      <div className="c-events-dashboard">
        {
          events.map((item, index) => (
            <div className="events-container" key={index}>
              <div className="day-title">{item.day}</div>
              <div className="events">
                {
                  item.eventsList.map((itemEvent, indexEvent) => (
                    <div className="event" key={indexEvent}>
                      <div className="status" />
                      <div className="right-event">
                        <div className="title">
                          <span className="name-event">{itemEvent.name}</span>
                          <img alt="/" src={facebook} />
                        </div>
                        <div className="time-event">
                          {itemEvent.time} - Campaing Launched
                        </div>
                        <div className="rule-event">
                          by rule: {itemEvent.rule}
                        </div>
                      </div>
                    </div>
                  ))
                }
              </div>
            </div>
          ))
        }
      </div>
    );
  }
}



