import './ApplyBigContainer.styl';

import React, {
  Component,
} from 'react';

import PropTypes from 'prop-types';

import Checkbox from 'material-ui/Checkbox';

const styleCheckBox = {
  labelStyle: {
    color: '#9aa3ac',
    fontSize: '14px'
  }
};

export default class ApplyBigContainer extends Component {
  static propTypes = {
    status: PropTypes.bool,
    changeStatus: PropTypes.func
  };

  render() {
    const {
      status,
      changeStatus
    } = this.props;

    return (
      <div className="c-apply-big-container">
        <div className="container">
          <Checkbox
            label="Connect this campaing to all rules"
            labelStyle={styleCheckBox.labelStyle}
            iconStyle={{ fill: status ? '#5bb995' : '#678198' }}
            onCheck={() => changeStatus()}
            checked={status}
            labelPosition="left"
          />
        </div>
      </div>
    );
  }
}



