import * as actionTypes from '../constants/UserConstants';
import _cloneDeep from 'lodash/cloneDeep';

export function addTeammate(object) {
  const userData = _cloneDeep(object);
  const numbers = object.team.mates.map((item) => item.id);
  let id = Math.max.apply(null, numbers);

  userData.team.mates.push({ email: '', status: false, id: ++id });
  return (dispatch) => {
    dispatch({
      type: actionTypes.ADD_USER_TEAMMATE,
      userData
    });
  };
}

export function removeTeammate(object, index) {
  console.log(object, index);
  const userData = _cloneDeep(object);

  userData.team.mates.splice(index, 1);
  return (dispatch) => {
    dispatch({
      type: actionTypes.REMOVE_USER_TEAMMATE,
      userData
    });
  };
}

export function setTeammateEmail(e, object, index) {
  const userData = _cloneDeep(object);

  userData.team.mates[index].email = e.target.value;
  return (dispatch) => {
    dispatch({
      type: actionTypes.SET_USER_TEAMMATE,
      userData
    });
  };
}

export function setTeammateStatus(object, index) {
  const userData = _cloneDeep(object);

  userData.team.mates[index].status = !userData.team.mates[index].status;
  return (dispatch) => {
    dispatch({
      type: actionTypes.SET_STATUS_TEAMMATE,
      userData
    });
  };
}



