import './DashboardPage.styl';

import React, {
  Component,
} from 'react';

import {
  Header,
  HeaderBottom,
  DashBoardRules,
  EventsDashboard
} from 'components';

import PropTypes from 'prop-types';

import { bindActionCreators }       from 'redux';
import { connect }                  from 'react-redux';

import {
  CompanyActions
} from 'actions';

@connect(state => ({
  companyData: state.companyReducer
}), dispatch => ({
  companyActions: bindActionCreators(CompanyActions, dispatch)
}))

export default class DashboardPage extends Component {
  static propTypes = {
    companyData: PropTypes.object,
    companyActions: PropTypes.object,
    history: PropTypes.object
  };

  render() {
    const {
      companyData
    } = this.props.companyData;

    const {
      companyActions,
      history
    } = this.props;

    return (
      <div className="c-dashboard-page">
        <div className="dashboard-page-container">
          <Header
            indexPage={0}
          />
          <HeaderBottom
            bigTitle="All campaings"
            smallTitle="Event log"
            left
            showsSearchBig
            selectBig
            selectSmall
          />
          <div className="dashboard-body-container">
            <div className="dashboard-left-container">
              <DashBoardRules
                data={companyData}
                actions={companyActions}
                history={history}
              />
            </div>
            <div className="dashboard-right-container">
              <EventsDashboard />
            </div>
          </div>
        </div>
      </div>
    );
  }
}



