import './HeaderBottom.styl';
import PropTypes from 'prop-types';

import React, {
  Component,
} from 'react';

import { Link } from 'react-router-dom';

import DropDownMenu from 'material-ui/DropDownMenu';
import MenuItem from 'material-ui/MenuItem';

const help = [
  {
    name: 'Support',
    link: '/help'
  },
  {
    name: 'Contact us',
    link: '/help/contact-us'
  }
];

const selectContent = [
  {
    select: [
      { value: 1, text: 'All platforms' },
      { value: 2, text: 'Facebook' },
      { value: 3, text: 'myTarget' },
    ]
  },
  {
    select: [
      { value: 1, text: 'Any status' },
      { value: 2, text: 'off' },
      { value: 3, text: 'on' },
    ]
  },
];

const selectSmallContent = [
  {
    select: [
      { value: 1, text: 'Any type' },
      { value: 2, text: 'Facebook' },
      { value: 3, text: 'myTarget' },
    ]
  }
];

const selectStyle = {
  style: {
    textTransform: 'none',
    fontWeight: 'normal',
    fontSize: '12px',
    color: '#678198'
  },
  underlineStyle: {
    border: '0px'
  }
};

export default class HeaderBottom extends Component {
  static propTypes = {
    bigTitle: PropTypes.string,
    smallTitle: PropTypes.string,
    oneBlock: PropTypes.bool,
    search: PropTypes.bool,
    left: PropTypes.bool,
    right: PropTypes.bool,
    path: PropTypes.string,
    window: PropTypes.bool,
    nav: PropTypes.bool,
    showSearchSmall: PropTypes.bool,
    showsSearchBig: PropTypes.bool,
    borderSmall: PropTypes.bool,
    borderBig: PropTypes.bool,
    selectBig: PropTypes.bool,
    selectSmall: PropTypes.bool
  };

  constructor(props) {
    super(props);

    this.state = {
      searchBig: false,
      searchSmall: false,
      value: 1
    };
  }

  showFilter(state) {
    this.setState({ [state]: !this.state[state] });
  }

  handleChange = (event, index, value) => this.setState({ value });

  selectContainer(item) {
    return (
      <div className="select">
        <DropDownMenu
          value={this.state.value}
          onChange={this.handleChange}
          style={selectStyle.style}
          underlineStyle={selectStyle.underlineStyle}
        >
          {
            item.select.map((itemSelect) => (
              <MenuItem
                value={itemSelect.value}
                primaryText={itemSelect.text}
                key={itemSelect.value}
              />
            ))
          }
        </DropDownMenu>
      </div>
    );
  }

  bigHeader() {
    const {
      bigTitle,
      showsSearchBig,
      borderBig,
      selectBig
    } = this.props;

    const {
      searchBig
    } = this.state;

    return (
      <div className={`big-block ${ borderBig && 'no-border' }`}>
        {bigTitle}
        <div className="multi-header">
          {
            selectBig &&
              <div className="select-container">
                <div className="select-container">
                  {
                    selectContent.map((item, index) => (
                      <div key={index}>
                        {this.selectContainer(item)}
                      </div>
                    ))
                  }
                </div>
              </div>
          }
          {
            showsSearchBig &&
              <div className="container-search">
                {
                  searchBig &&
                    <div>
                      <input
                        className="input-header"
                        type="text"
                        autoFocus={true}
                        placeholder="Search"
                      />
                    </div>
                }
                <i
                  className="material-icons search"
                  onClick={this.showFilter.bind(this, 'searchBig')}
                >
                  {searchBig ? 'clear' : 'search'}
                </i>
                {
                  searchBig && <div className="border" />
                }
              </div>
          }
        </div>
      </div>
    );
  }

  smallHeader() {
    const {
      smallTitle,
      showSearchSmall,
      borderSmall,
      selectSmall
    } = this.props;

    const {
      searchSmall
    } = this.state;

    return (
      <div className={`small-block ${ borderSmall && 'no-border' }`}>
        {!searchSmall && smallTitle}
        <div className={`multi-header ${ searchSmall && 'full-width' }`}>
          {
            selectSmall &&
              <div className="select-container">
                <div className="select-container">
                  {
                    selectSmallContent.map((item, index) => (
                      <div key={index}>
                        {this.selectContainer(item)}
                      </div>
                    ))
                  }
                </div>
              </div>
          }
          {
            showSearchSmall &&
              <div className={`container-search ${ searchSmall && 'hidden-title' }`}>
                {
                  searchSmall &&
                    <div className="small-input">
                      <input
                        className="input-header"
                        type="text"
                        autoFocus={true}
                        placeholder="Search"
                      />
                    </div>
                }
                <i
                  className="material-icons search"
                  onClick={this.showFilter.bind(this, 'searchSmall')}
                >
                  {searchSmall ? 'clear' : 'search'}
                </i>
                {
                  searchSmall && <div className="border" />
                }
              </div>
          }
        </div>
      </div>
    );
  }

  render() {
    const {
      bigTitle,
      oneBlock,
      left,
      right,
      path,
      window,
      nav
    } = this.props;

    return (
      <div className="c-header-bottom">
        <div className="wrapper-header">
          {
            nav &&
              <div className="help-nav">
                {
                  help.map((item, index) => (
                    <Link to={item.link} key={index} className="help-item" >
                      {item.name}
                      {
                        path === item.link &&
                          <div className="link-divider" />
                      }
                      {
                        path !== '/help/contact-us' && index !== 1 &&
                          <div className="link-divider" />
                      }
                    </Link>
                  ))
                }
              </div>
          }
          {
            oneBlock &&
              <div className={`big-block ${ window && 'big-block-window' }`}>
                {bigTitle}
              </div>
          }
          {
            left &&
              <div className="container-header">
                {this.bigHeader()}
                {this.smallHeader()}
              </div>
          }
          {
            right &&
              <div className="container-header">
                {this.smallHeader()}
                {this.bigHeader()}
              </div>
          }
        </div>
      </div>
    );
  }
}



