import './SocialRule.styl';

import React, {
  Component,
} from 'react';
import slack from '../../../../images/slack.png';
import telegram from '../../../../images/telegram.png';
import Checkbox from 'material-ui/Checkbox';

const styleCheckBox = {
  style: {
    textAlign: 'right'
  },
  labelStyle: {
    color: '#9aa3ac',
    fontSize: '16px'
  },
  iconStyle: {
    fill: '#678198',
    right: '-24px'
  }
};

export default class SocialRule extends Component {
  static propTypes = {
  };

  constructor(props) {
    super(props);

    this.state = {
      social: [
        { name: 'Telegram', status: false, active: true, img: telegram },
        { name: 'Slack', status: false, active: false, img: slack }
      ]
    };
  }

  updateCheck(index) {
    const social = this.state.social;

    social[index].status = !social[index].status;
    this.setState({ social });
  }

  addSocial(index) {
    const social = this.state.social;

    social[index].active = !social[index].active;
    this.setState({ social });
  }

  render() {
    const {
      social
    } = this.state;

    return (
      <div className="c-social-rule">
        {
          social.map((item, index) => (
            <div className="social-container" key={index}>
              <div className="social-left">
                <div className="social-img">
                  <img alt="/" src={item.img} />
                </div>
                <span className="social-name">{item.name}</span>
              </div>
              <div className="social-right">
                {
                  item.active ?
                    <div className="check-box-container">
                      <Checkbox
                        label="Use"
                        style={styleCheckBox.style}
                        labelStyle={styleCheckBox.labelStyle}
                        iconStyle={{ fill: item.status ? '#5bb995' : '#678198' }}
                        onCheck={this.updateCheck.bind(this, index)}
                        checked={item.status}
                        labelPosition="left"
                      />
                    </div>
                    :
                    <div
                      className="add-social"
                      onClick={this.addSocial.bind(this, index)}
                    >
                      <span className="add-btn-text">Add</span>
                      <i
                        className="material-icons"
                      >
                        add_circle
                      </i>
                    </div>
                }
              </div>
            </div>
          ))
        }
      </div>
    );
  }
}



