import './Report.styl';

import React, {
  Component,
} from 'react';

import DropDownMenu from 'material-ui/DropDownMenu';
import MenuItem from 'material-ui/MenuItem';
import ReportChanels from './ReportChanels/ReportChanels';
import ReportPeriod from './ReportPeriod/ReportPeriod';
import IconButton from 'material-ui/IconButton';

const stylesDropDown = {
  style: {
    width: '100%'
  },
  labelStyle: {
    color: '#000',
    fontSize: '24px',
    padding: 0
  },
  underlineStyle: {
    width: '700px',
    margin: 0
  },
  selectedMenuItemStyle: {
    color: '#5bb995'
  },
  description: {
    color: '#9aa3ac',
    fontSize: '10px',
    marginTop: '-17px'
  },
  iconStyle: {
    right: '-16px'
  }
};

const menuItem = [
  {
    title: 'Installs report',
    description: `Calculates mobile app installs for all ads
     in connected ads accounts (mobile_app_installs event)`
  },
  {
    title: 'Website visitors dynamics report',
    description: 'Measures dynamics of visitors of your website (web_link_clicks events)'
  },
  {
    title: 'CPA report',
    description: `Calculates CPA (cost per action) dynamics
     (total spend / number of mobile_checkout_initiated events)`
  }
];

export default class Report extends Component {
  static propTypes = {
  };

  constructor(props) {
    super(props);

    this.state = {
      value: 0
    };
  }

  handleChange = (event, index, value) => this.setState({ value });

  render() {
    const {
      value
    } = this.state;

    return (
      <div className="c-report-page">
        <span className="title-report">
          You have no reports yet
        </span>
        <span className="description-report">
          create the first one
        </span>
        <div className="select-report">
          <DropDownMenu
            value={value}
            onChange={this.handleChange}
            style={stylesDropDown.style}
            autoWidth={false}
            underlineStyle={stylesDropDown.underlineStyle}
            maxHeight={300}
            labelStyle={stylesDropDown.labelStyle}
            selectedMenuItemStyle={stylesDropDown.selectedMenuItemStyle}
            iconStyle={stylesDropDown.iconStyle}
          >
            {
              menuItem.map((item, index) => (
                <MenuItem
                  key={index}
                  value={index}
                  primaryText={<div style={stylesDropDown.description}>{item.description}</div>}
                  children={<div>{item.title}</div>}
                  label={item.title}
                />
              ))
            }
          </DropDownMenu>
          <div className="help-container">
            <IconButton tooltip="ToolTip">
              <i className="material-icons help-rule">help</i>
            </IconButton>
          </div>
        </div>
        <div className="report-settings-container">
          <div className="settings">
            <span className="name-settings">Chanels</span>
            <ReportChanels />
          </div>
          <div className="settings">
            <span className="name-settings">Period</span>
            <ReportPeriod />
          </div>
        </div>
      </div>
    );
  }
}



