import './DashBoardRules.styl';
import React, {
  Component,
} from 'react';
import facebook from '../../../images/icons/facebook/facebook-grey.svg';
import mytarget from '../../../images/icons/small/mytarget.svg';
import Toggle from 'material-ui/Toggle';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

const stylesToggle = {
  block: {
    maxWidth: 250,
  },
  toggle: {
    marginBottom: 16,
  },
  thumbOff: {
    backgroundColor: '#fff',
  },
  trackOff: {
    backgroundColor: '#e0e8ee',
  },
  thumbSwitched: {
    backgroundColor: '#fff',
  },
  trackSwitched: {
    backgroundColor: '#5bb995',
  },
  labelStyle: {
    color: '#678198',
    fontSize: '12px',
    marginLeft: '12px'
  },
};

export default class DashBoardRules extends Component {
  static propTypes = {
    data: PropTypes.array,
    actions: PropTypes.object,
    history: PropTypes.object
  };

  constructor(props) {
    super(props);

    this.state = {
      indexState: null
    };
  }

  openContainer(index) {
    if (index === this.state.indexState) {
      this.setState({ indexState: null });
    } else {
      this.setState({ indexState: index });
    }
  }

  routeToEdit(id) {
    const {
      getCompany
    } = this.props.actions;

    const {
      data,
      history
    } = this.props;

    getCompany(data, id);
    history.push('/rules');
  }

  render() {
    const {
      indexState
    } = this.state;

    const {
      data
    } = this.props;

    const {
      setCompanyStatus
    } = this.props.actions;

    return (
      <div className="c-dash-board-rules">
        {
          data.map((item, index) => (
            <div
              className={`rule-block-static ${ indexState === index && 'rule-block-static-active' }`}
              key={index}
            >
              <div
                className={`rule-block ${ indexState === index && 'rule-block-active' }`}
              >
                <i
                  className={`material-icons arrow ${ indexState === index && 'arrow-active' }`}
                  onClick={this.openContainer.bind(this, index)}
                >
                  keyboard_arrow_down
                </i>
                <div className="rule-top">
                  <div className="rule-top-left" onClick={this.openContainer.bind(this, index)}>
                    <div className="platform-icon">
                      <img alt="/" src={item.facebook ? facebook : mytarget} />
                    </div>
                    <div className="container-name">
                      <span className="rule-name">{item.name}</span>
                    </div>
                  </div>
                  <div onClick={this.routeToEdit.bind(this, item.id)} className="rule-top-right">Edit</div>
                </div>
                <div className="rule-toggle-container">
                  <Toggle
                    label={`Campaing status: ${ item.status ? 'ON' : 'OFF' }`}
                    thumbStyle={stylesToggle.thumbOff}
                    trackStyle={stylesToggle.trackOff}
                    thumbSwitchedStyle={stylesToggle.thumbSwitched}
                    trackSwitchedStyle={stylesToggle.trackSwitched}
                    labelStyle={stylesToggle.labelStyle}
                    labelPosition="right"
                    onToggle={() => setCompanyStatus(data, index)}
                    toggled={item.status}
                  />
                </div>
                <span
                  className="aplied-rules"
                  onClick={this.openContainer.bind(this, index)}
                >
                  Aplied rules
                </span>
                <div
                  className={`aplied-rules-container ${ indexState === index && 'aplied-rules-container-active' }`}
                >
                  {
                    item.rules.map((ruleItem, ruleIndex) => (
                      <Link
                        to={`/edit-rule/${ ruleItem.id }`}
                        className="rule-name"
                        key={ruleIndex}
                      >
                        {ruleItem.name}
                      </Link>
                    ))
                  }
                </div>
              </div>
            </div>
          ))
        }
      </div>
    );
  }
}



