import './RuleSettingsBody.styl';

import TextField from 'material-ui/TextField';
import { RadioButton, RadioButtonGroup } from 'material-ui/RadioButton';
import RuleCodeBlock from './RuleCodeBlock/RuleCodeBlock';
import Unchecked from 'material-ui/svg-icons/toggle/radio-button-unchecked';
import Checked from 'material-ui/svg-icons/toggle/radio-button-checked';
import Toggle from 'material-ui/Toggle';
import FlatButton from 'material-ui/FlatButton';
import SocialRule from './SocialRule/SocialRule';
import RuleKeyComponent from './RuleKeyComponent/RuleKeyComponent';
import PropTypes from 'prop-types';

import React, {
  Component,
} from 'react';

const stylesField = {
  style: {
    color: '#000000',
    width: '100%',
    fontSize: '24px'
  },
  floatingLabelStyle: {
    color: '#000000',
  },
  underlineStyle: {
    borderColor: '#d1dfea'
  },
  underlineFocusStyle: {
    borderColor: '#678198'
  },
  floatingLabelFocusStyle: {
    color: '#678198',
  },
};

const btnStyle = {
  style: {
    marginTop: '40px',
    color: '#fff',
    borderRadius: '4px',
    width: '110px',
    height: '40px'
  },
  labelStyle: {
    fontSize: '16px'
  }
};

const stylesRadio = {
  radioButton: {
    width: 'auto',
    marginRight: '24px'
  },
  labelStyle: {
    color: '#000',
    fontSize: '16px'
  },
  iconStyle: {
    marginRight: '13px',
    fill: '#678198'
  }
};

const stylesToggle = {
  block: {
    maxWidth: 250,
  },
  toggle: {
    marginBottom: 16,
  },
  thumbOff: {
    backgroundColor: '#fff',
  },
  trackOff: {
    backgroundColor: '#e0e8ee',
  },
  thumbSwitched: {
    backgroundColor: '#fff',
  },
  trackSwitched: {
    backgroundColor: '#5bb995',
  },
  labelStyle: {
    color: '#000',
    fontSize: '16px',
    marginLeft: '12px'
  },
};

const platform = [
  'Facebook',
  'myTarget',
  'Joint'
];

export default class RuleSettingsBody extends Component {
  static propTypes = {
    status: PropTypes.bool,
    changeStatus: PropTypes.func,
    rule: PropTypes.object,
    params: PropTypes.string
  };

  constructor(props) {
    super(props);

    this.state = {
      notificationShow: false,
      allCompany: false,
      keyShow: false
    };
  }

  setToggle(state) {
    this.setState({ [state]: !this.state[state] });
  }

  render() {
    const {
      notificationShow,
      keyShow
    } = this.state;

    const {
      status,
      changeStatus,
      rule,
      params
    } = this.props;

    return (
      <div className="c-rule-settings-body">
        <div className="title-rule-settings">
          <span className="title">Create your first rule</span>
          <span className="description">and choose campaings you want to apply</span>
        </div>
        <div className="container-name-field">
          <TextField
            hintText="Rule name"
            floatingLabelText="Rule name"
            style={stylesField.style}
            floatingLabelStyle={stylesField.floatingLabelStyle}
            underlineStyle={stylesField.underlineStyle}
            floatingLabelFocusStyle={stylesField.floatingLabelFocusStyle}
            underlineFocusStyle={stylesField.underlineFocusStyle}
            value={params && rule.name}
          />
        </div>
        <div className="rule-platform">
          <span className="platform-text">
            Platform
          </span>
          <RadioButtonGroup name="shipSpeed" defaultSelected={0} className="platform-container">
            {
              platform.map((item, index) => (
                <RadioButton
                  key={index}
                  value={index}
                  label={item}
                  style={stylesRadio.radioButton}
                  labelStyle={stylesRadio.labelStyle}
                  iconStyle={stylesRadio.iconStyle}
                  checkedIcon={<Checked style={{ fill: '#5bb995' }} />}
                  uncheckedIcon={<Unchecked />}
                />
              ))
            }
          </RadioButtonGroup>
        </div>
        <RuleCodeBlock
          rule={rule}
          params={params}
        />
        <div className="rule-controller-block">
          <Toggle
            label="Notification"
            thumbStyle={stylesToggle.thumbOff}
            trackStyle={stylesToggle.trackOff}
            thumbSwitchedStyle={stylesToggle.thumbSwitched}
            trackSwitchedStyle={stylesToggle.trackSwitched}
            labelStyle={stylesToggle.labelStyle}
            labelPosition="right"
            onToggle={this.setToggle.bind(this, 'notificationShow')}
            toggled={notificationShow}
          />
          {
            notificationShow &&
              <SocialRule />
          }
          <Toggle
            label="Apply to all new campaings"
            thumbStyle={stylesToggle.thumbOff}
            trackStyle={stylesToggle.trackOff}
            thumbSwitchedStyle={stylesToggle.thumbSwitched}
            trackSwitchedStyle={stylesToggle.trackSwitched}
            labelStyle={stylesToggle.labelStyle}
            labelPosition="right"
            onToggle={() => changeStatus()}
            toggled={status}
          />
          <Toggle
            label="Apply to all campaings which contain"
            thumbStyle={stylesToggle.thumbOff}
            trackStyle={stylesToggle.trackOff}
            thumbSwitchedStyle={stylesToggle.thumbSwitched}
            trackSwitchedStyle={stylesToggle.trackSwitched}
            labelStyle={stylesToggle.labelStyle}
            labelPosition="right"
            onToggle={this.setToggle.bind(this, 'keyShow')}
            toggled={keyShow}
          />
          {
            keyShow &&
              <RuleKeyComponent />
          }
        </div>
        <div className="btn-done">
          <FlatButton
            label="Done"
            backgroundColor="#f07171"
            style={btnStyle.style}
            labelStyle={btnStyle.labelStyle}
          />
        </div>
      </div>
    );
  }
}



