import * as actionTypes from '../constants/CompanyConstants';
import _cloneDeep from 'lodash/cloneDeep';
import _find from 'lodash/find';

export function setCompanyStatus(array, index) {
  const companyData = _cloneDeep(array);

  companyData[index].status = !companyData[index].status;
  return (dispatch) => {
    dispatch({
      type: actionTypes.SET_COMPANY_STATUS,
      companyData
    });
  };
}

export function getCompany(array, id) {
  let company;
  if (id) {
    company = _find(array, { id: +id });
  } else {
    company = array;
  }
  return (dispatch) => {
    dispatch({
      type: actionTypes.GET_COMPANY_OBJECT,
      company
    });
  };
}

export function getRule(array, id) {
  const rule = _find(array, { id: +id });

  return (dispatch) => {
    dispatch({
      type: actionTypes.GET_RULE_OBJECT,
      rule
    });
  };
}



