import './Accounts.styl';
import MenuItem from 'material-ui/MenuItem';
import IconMenu from 'material-ui/IconMenu';
import IconButton from 'material-ui/IconButton';
import PropType from 'prop-types';
import facebookIcon from '../../../../images/icons/big/facebook-2.svg';
import myTarget from '../../../../images/icons/big/MyTargetLogo.svg';

import React, {
  Component,
} from 'react';

const accountContent = [
  {
    logo: facebookIcon,
    text: 'Facebook',
    settingsText: 'Account information',
    icon: 'settings'
  },
  {
    logo: myTarget,
    text: 'myTarget',
    settingsText: 'Connect',
    icon: 'add_circle'
  },
];

export default class Accounts extends Component {
  static propTypes = {
    data: PropType.object
  };

  iconMenu(item, index) {
    return (
      <i
        className={`material-icons account-icon ${ index === 1 && 'account-icon-green' }`}
      >
        {item.icon}
      </i>
    );
  }
  render() {
    const {
      facebook
    } = this.props.data;

    return (
      <div className="c-accounts">
        {
          accountContent.map((item, index) => (
            <div className="account-container" key={index}>
              <div className={`account-left ${ index === 1 && 'account-left-active' }`}>
                <div className="logo-soc">
                  <img alt="/" className={index === 1 && 'small-img'} src={item.logo} />
                </div>
                <span className="account-text">{item.text}</span>
              </div>
              <div className="account-right">
                <span className="account-settings-text">{item.settingsText}</span>
                <IconMenu
                  iconButtonElement={<IconButton>{this.iconMenu(item, index)}</IconButton>}
                >
                  {
                    index === 0 ?
                      <div>
                        <div className="info-block">
                          <div className="account-id">
                            id: {facebook.id}
                          </div>
                          <div className="account-name">
                            name: {facebook.name}
                          </div>
                        </div>
                        <MenuItem
                          value="1"
                          primaryText="Disconnect"
                          style={{ color: '#f07171' }}
                        />
                      </div>
                      :
                      <div>
                        <div className="info-block">
                          <div className="account-not-connect">
                            Not connect
                          </div>
                        </div>
                        <MenuItem value="1" primaryText="Connect" />
                      </div>
                  }
                </IconMenu>
              </div>
            </div>
          ))
        }
      </div>
    );
  }
}



