import './BodyRulesList.styl';

import React, {
  Component,
} from 'react';

import FloatingActionButton from 'material-ui/FloatingActionButton';
import ContentAdd from 'material-ui/svg-icons/content/add';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';

const style = {
  marginTop: '30px',
  marginLeft: '40px',
  marginBottom: '30px'
};

import Rule from './Rule/Rule';

export default class BodyRulesList extends Component {
  static propTypes = {
    rule: PropTypes.array,
    company: PropTypes.object,
    statusAllRules: PropTypes.bool
  };

  render() {
    const {
      rule,
      company,
      statusAllRules
    } = this.props;

    return (
      <div className="c-body-rules-list">
        {
          rule.map((item, index) => (
            <Rule
              rule={item}
              key={index}
              company={company}
              statusAllRules={statusAllRules}
            />
          ))
        }
        <Link to="/rule-settings">
          <FloatingActionButton
            style={style}
            backgroundColor="#f07171"
          >
            <ContentAdd />
          </FloatingActionButton>
        </Link>
      </div>
    );
  }
}



