import './Team.styl';
import PropTypes from 'prop-types';

import React, {
  Component,
} from 'react';

import TextField from 'material-ui/TextField';
import Checkbox from 'material-ui/Checkbox';

const stylesField = {
  style: {
    color: '#000000',
    width: '100%',
    fontSize: '16px',
    marginTop: '-14px'
  },
  floatingLabelStyle: {
    color: '#000000',
  },
  underlineStyle: {
    borderColor: '#d1dfea'
  },
  underlineFocusStyle: {
    borderColor: '#678198'
  },
  floatingLabelFocusStyle: {
    color: '#678198',
  },
};

const styleCheckBox = {
  style: {
    width: '180px',
    marginLeft: '-5px'
  },
  labelStyle: {
    color: '#9aa3ac',
    fontSize: '14px',
    marginLeft: '-8px',
    marginTop: '1px'
  },
  iconStyle: {
    fill: '#678198',
    width: '16px',
    height: '16px'
  }
};

export default class Team extends Component {
  static propTypes = {
    data: PropTypes.object,
    actions: PropTypes.object
  };

  render() {
    const {
      team
    } = this.props.data;

    const {
      addTeammate,
      removeTeammate,
      setTeammateEmail,
      setTeammateStatus
    } = this.props.actions;

    return (
      <div className="c-team">
        <div className="team-name-container">
          <TextField
            hintText="Name"
            floatingLabelText="Name"
            style={stylesField.style}
            floatingLabelStyle={stylesField.floatingLabelStyle}
            underlineStyle={stylesField.underlineStyle}
            floatingLabelFocusStyle={stylesField.floatingLabelFocusStyle}
            underlineFocusStyle={stylesField.underlineFocusStyle}
            value={team.name}
          />
        </div>
        <div>
          {
            team.mates.map((item, index) => (
              <div className="person" key={item.id}>
                <div className="form-container">
                  <TextField
                    hintText="Teammate e-mail"
                    floatingLabelText="Teammate e-mail"
                    style={stylesField.style}
                    floatingLabelStyle={stylesField.floatingLabelStyle}
                    underlineStyle={stylesField.underlineStyle}
                    floatingLabelFocusStyle={stylesField.floatingLabelFocusStyle}
                    underlineFocusStyle={stylesField.underlineFocusStyle}
                    onChange={(e) => setTeammateEmail(e, this.props.data, index)}
                    value={item.email}
                  />
                </div>
                <div className="check-box-container">
                  <Checkbox
                    label="Make an admin"
                    style={styleCheckBox.style}
                    labelStyle={styleCheckBox.labelStyle}
                    iconStyle={{ fill: item.status ? '#5bb995' : '#678198' }}
                    onCheck={() => setTeammateStatus(this.props.data, index)}
                    checked={item.status}
                  />
                </div>
                <i
                  className="material-icons"
                  onClick={() => removeTeammate(this.props.data, index)}
                >
                  cancel
                </i>
              </div>
            ))
          }
        </div>
        <div
          className="add-container"
          onClick={() => addTeammate(this.props.data)}
        >
          <i
            className="material-icons"
          >
            add_circle
          </i>
          <span className="add-btn-text">Add member</span>
        </div>
      </div>
    );
  }
}



