import './MenuHelp.styl';

import React, {
  Component,
} from 'react';

import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';

const menu = [
  { name: 'Rule', link: '/help' },
  { name: 'Report', link: '/help/two' }
];

export default class MenuHelp extends Component {
  static propTypes = {
    path: PropTypes.string
  };

  render() {
    const {
      path
    } = this.props;

    return (
      <div className="c-menu-help">
        {
          menu.map((item, index) => (
            <Link
              to={item.link}
              className={`menu-item ${ path === item.link && 'menu-item-active' }`}
              key={index}
            >
              {item.name}
            </Link>
          ))
        }
      </div>
    );
  }
}



